.main-menu {

  ul {
    display: flex;
    padding: 0;
    margin: 0;

    li {
      list-style: none;
      font-size: 21px;
      line-height: 1;

      a {
        display: flex;
        align-items: center;
        text-decoration: none;
        color: $link-gray;
        transition: color .2s;
        cursor: pointer;

        &:hover {
          color: $blue;
        }

        img {
          margin-right: 6px;
        }
      }
    }
  }
}
